let Annotate = {};
let isAnnotationStarted = false;
const canvasOptions = { width: 0, height: 0 };
var annotationConfig = {
  toolType: "draw",
  isErase: false,
  drawMethod: "pencil",
  strokeColor: "#ff0000",
  lineWidth: 3,
  hasInput: false,
  lastMouseX: 0,
  lastMouseY: 0,
  canvasX: 0,
  canvasY: 0,
  mouseX: 0,
  mouseY: 0,
  mouseDown: false,
  drawElement: null,
  drawCtx: null,
  canvasDomID: null,
  canvasVideoPlayerID: null,
  customCanvasID: "draw_veneer2"
};

var setupAnnotationToolbar = function () {
  var toolbarCont = document.createElement("div");
  toolbarCont.setAttribute("id", "annotationToolbarContainer");
  toolbarCont.style = `position: absolute;bottom: 0; z-index: 98;`;
  var toolbar = document.createElement("div");
  toolbar.setAttribute("id", "toolbar");
  toolbar.innerHTML = `<div class="atn-btns">
      <button id="annotate-draw" onclick="annotateToolAction('tool','draw')">Draw</button>
      <select id="selWidth" onchange="annotateToolAction('drawMethod',this.value);">
        <option value="pencil" selected="selected">Pencil</option>
        <option value="text">Text</option>
      </select>
      <input
      type="color"
      id="favcolor"
      name="favcolor"
      value="#ff0000"
      onchange="annotateToolAction('strokeColor', this.value);"
    />
    <select id="selWidth" onchange="annotateToolAction('lineWidth', this.value);">
      <option value="1">1</option>
      <option value="3" selected="selected">3</option>
      <option value="5">5</option>
      <option value="7">7</option>
      <option value="9">9</option>
      <option value="11">11</option>
    </select>
    <input type="button" value="Erase" onclick="annotateToolAction('erase')" />
    <input type="button" value="Clear" onclick="annotateToolAction('clear');" />
      </div>`;
  toolbarCont.append(toolbar);
  return { toolbarCont, toolbar };
};

function annotateAddInput(x, y) {
  var input = document.createElement("input");
  input.className = "annotate-text";
  input.type = "text";
  input.style.position = "fixed";
  input.style.zIndex = 50;
  input.style.left = x - 4 + "px";
  input.style.top = y - 4 + "px";
  input.onkeydown = handleEnter;
  document.body.appendChild(input);
  input.focus();
  annotationConfig.hasInput = true;
}

function handleEnter(e) {
  var keyCode = e.keyCode;
  if (keyCode === 13) {
    annotateDrawText(this.value);
    document.body.removeChild(this);
    annotationConfig.hasInput = false;
  } else if (keyCode === 27) {
    document.body.removeChild(this);
    annotationConfig.hasInput = false;
  }
}

function annotateDrawText(txt) {
  annotationConfig.drawCtx.textBaseline = "top";
  annotationConfig.drawCtx.textAlign = "left";
  annotationConfig.drawCtx.fillStyle = annotationConfig.strokeColor;
  annotationConfig.drawCtx.font = "14px sans-serif";
  annotationConfig.drawCtx.fillText(
    txt,
    annotationConfig.lastMouseX - 4,
    annotationConfig.lastMouseY - 4
  );
}

function reOffset(selector) {
  const canvasData = selector.getBoundingClientRect();
  annotationConfig.canvasX = canvasData.left;
  annotationConfig.canvasY = canvasData.top;
}

Annotate.toolBarAction = function (action, value) {
  if (action === "tool") {
    annotationConfig.toolType = value;
    annotationConfig.isErase = false;
  } else if (action === "erase") {
    annotationConfig.toolType = action;
    annotationConfig.isErase = true;
  } else if (action === "strokeColor") {
    annotationConfig.strokeColor = value;
  } else if (action === "lineWidth") {
    annotationConfig.lineWidth = value;
  } else if (action === "drawMethod") {
    annotationConfig.drawMethod = value;
  } else if (action === "clear") {
    const elements = document.getElementsByClassName("annotate-text");
    while (elements.length > 0) elements[0].remove();
    annotationConfig.drawCtx.clearRect(
      0,
      0,
      canvasOptions.width,
      canvasOptions.height
    );
  }
};

Annotate.appendCustomCanvas = function (wrapper, className, width, height) {
  canvasOptions.width = width;
  canvasOptions.height = height;
  let drawcanvas = document.createElement("canvas");
  drawcanvas.id = annotationConfig.customCanvasID;
  drawcanvas.width = width;
  drawcanvas.height = height;
  drawcanvas.className = className;
  drawcanvas.setAttribute("style", `display:block; user-select: none;`);
  drawcanvas.style.zIndex = 5000;
  wrapper.append(drawcanvas);
  let { toolbarCont, toolbar } = setupAnnotationToolbar();
  wrapper.append(toolbarCont);
};

function onDown(e, touch) {
  e.preventDefault();
  const clientX = touch ? event.touches[0].clientX : e.clientX;
  const clientY = touch ? event.touches[0].clientY : e.clientY;

  reOffset(annotationConfig.drawElement);
  annotationConfig.lastMouseX = annotationConfig.mouseX = parseInt(
    clientX - annotationConfig.canvasX
  );
  annotationConfig.lastMouseY = annotationConfig.mouseY = parseInt(
    clientY - annotationConfig.canvasY
  );
  annotationConfig.mouseDown = true;
  if (
    annotationConfig.drawMethod === "text" &&
    annotationConfig.toolType === "draw"
  ) {
    annotateAddInput(clientX, clientY);
  }
}
function onMove(e, inputContext, touch) {
  e.preventDefault();
  const clientX = touch ? event.touches[0].clientX : e.clientX;
  const clientY = touch ? event.touches[0].clientY : e.clientY;
  annotationConfig.mouseX = parseInt(clientX - annotationConfig.canvasX);
  annotationConfig.mouseY = parseInt(clientY - annotationConfig.canvasY);

  if (annotationConfig.mouseDown && annotationConfig.drawMethod === "pencil") {
    annotationConfig.drawCtx.beginPath();
    if (annotationConfig.toolType == "draw") {
      annotationConfig.drawCtx.globalCompositeOperation = "source-over";
      annotationConfig.drawCtx.strokeStyle = annotationConfig.strokeColor;
      annotationConfig.drawCtx.lineWidth = annotationConfig.lineWidth;
    } else {
      annotationConfig.drawCtx.globalCompositeOperation = "destination-out";
      annotationConfig.drawCtx.lineWidth = 10;
    }

    annotationConfig.drawCtx.moveTo(
      annotationConfig.lastMouseX,
      annotationConfig.lastMouseY
    );
    annotationConfig.drawCtx.lineTo(
      annotationConfig.mouseX,
      annotationConfig.mouseY
    );
    annotationConfig.drawCtx.lineJoin = inputContext.lineCap = "round";
    annotationConfig.drawCtx.stroke();
  }
  annotationConfig.lastMouseX = annotationConfig.mouseX;
  annotationConfig.lastMouseY = annotationConfig.mouseY;
}
function onUp(e) {
  e.preventDefault();
  annotationConfig.mouseDown = false;
}
/*Draw on mouse move*/
Annotate.mouseAnnotate = function (canvasDomID, inputContext, canvasVideoPlayerID) {
  isAnnotationStarted = true;
  annotationConfig.drawElement = document.getElementById(annotationConfig.customCanvasID);
  annotationConfig.drawCtx = annotationConfig.drawElement.getContext("2d");
  annotationConfig.canvasDomID = canvasDomID;
  annotationConfig.canvasVideoPlayerID = canvasVideoPlayerID;

  //on onmousedown
  annotationConfig.drawElement.onmousedown = function (e) {
    onDown(e, false);
  };
  annotationConfig.drawElement.ontouchstart = function (e) {
    onDown(e, true);
  };

  //on onmousemove
  annotationConfig.drawElement.onmousemove = function (e) {
    onMove(e, inputContext, false);
  };
  annotationConfig.drawElement.ontouchmove = function (e) {
    onMove(e, inputContext, true);
  };

  //on mouseup
  annotationConfig.drawElement.onmouseup = function (e) {
    onUp(e);
  };
  annotationConfig.drawElement.ontouchcancel = function (e) {
    onUp(e);
  };
  return annotationConfig.drawElement;
};

Annotate.stopAnnotation = function () {
  isAnnotationStarted = false;
  var canvasWrapper = document.getElementById("canvas-wrapper");
  canvasWrapper ? canvasWrapper.remove() : null;
  const elements = document.getElementsByClassName("annotate-text");
  while (elements.length > 0) elements[0].remove();
  annotationConfig.toolType = "draw";
  annotationConfig.isErase = false;
  annotationConfig.drawMethod = "pencil";
  annotationConfig.strokeColor = "#ff0000";
  annotationConfig.lineWidth = 3;
};

function resize(){
 if(isAnnotationStarted){
  setTimeout(function(){ 
    const drawCanvas = document.getElementById(annotationConfig.customCanvasID);
    const streamCanvas = document.getElementById(annotationConfig.canvasDomID);
    const videoCanvas = document.getElementById(annotationConfig.canvasVideoPlayerID);
    drawCanvas.width = videoCanvas.clientWidth;
    drawCanvas.height = videoCanvas.clientHeight;
    streamCanvas.width = videoCanvas.clientWidth;
    streamCanvas.height = videoCanvas.clientHeight;
    reOffset(annotationConfig.drawElement);
   }, 100);
 }
}
window.addEventListener("resize", resize);

export default Annotate;



