import Logger from './utils/Logger';
import Room from './Room';
import { Event, RoomEvent, StreamEvent } from './Events';
import Stream from './Stream';
import ViewProperties from './views/ViewProperties';
import VideoPlayer from './views/VideoPlayer'
import VcxQuickApi from './VcxQuickApi';
import config from './../Enx.config.js';
// eslint-disable-next-line 
require('expose-loader?adapter!../lib/adapter.js');
// eslint-disable-next-line
require('script-loader!./utils/L.Resizer.js');

const EnxRtc = {
  Logger,
  EnxRoom: Room.bind(null, undefined, undefined),
    Event,
    RoomEvent,
    StreamEvent,
    EnxStream: Stream.bind(null, undefined),
    VideoPlayer,
    ViewProperties,
    isSupported : config.is_supported,
    version : config.product.version,
    getDevices: VcxQuickApi.getDevice,
    switchMediaDevice:VcxQuickApi.switchMediaDevice,
    notifyDeviceUpdate:VcxQuickApi.notifyDeviceUpdate,
    joinRoom:VcxQuickApi.joinRoom,
    listParticipants:VcxQuickApi.listParticipants,
};
export default EnxRtc;



